/* eslint-disable @typescript-eslint/no-var-requires */
const React = require('react');
const { MapContainer, TileLayer, Marker, Popup, useMap } = typeof window !== undefined && require('react-leaflet');
//const L = typeof window !== undefined && require('leaflet');

function InvalidateSize({ open }) {
  const map = useMap();
 // console.log('map center:', map.getCenter());
  React.useEffect(() => {
    open === true ? map.invalidateSize() : null;
  }, [open]);

  return null;
}
export const Leaflet = ({ className, lat, lon, open }) => {
  //const mapRef = React.useRef(null);
  const position: [number, number] = [lat, lon];

  if (typeof window !== undefined)
    return (
      <MapContainer className={className} center={position} zoom={13}>
        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
        <InvalidateSize open={open} />
        <Marker position={position}>
          <Popup>
            آهن با ما
            <br /> دفتر مرکزی
          </Popup>
        </Marker>
      </MapContainer>
    );
  return <div style={{ width: '270px', height: '200px' }}>Loading...</div>;
};
