import * as React from 'react';
import addToMailchimp from 'gatsby-plugin-mailchimp';

export function useSubscribe() {
  const [email, setEmail] = React.useState(null);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    addToMailchimp(email)
      .then(data => {
        //console.log(`${data.result}: ${data.msg}`);

        if (data.result === 'success') {
          alert('اشتراک شما با موفقیت انجام شد');

          return;
        } else if (data.result === 'error' && data.msg.includes(email)) {
          alert('شما در حال حاضر مشترک آهن باما هستید');
          return;
        } else if (data.result === 'error' && data.msg.includes('too many')) {
          alert('تلاش بیش از حد مجاز. لطفا بعدا تلاش کنید');
          return;
        } else {
          alert('اشتراک ناموق ');
          console.log(data.msg);
          return;
        }
      })
      .catch((error: Error) => {
        console.log(error);
        // Errors in here are client side
        // Mailchimp always returns a 200
      });
  };
  const handleEmail = (event: React.ChangeEvent<HTMLInputElement>) => {
    setEmail(event.target.value);
  };

  return { handleSubmit, handleEmail };
}
