import { Link } from 'gatsby';
import * as React from 'react';
//import { shopInfo, navbarMenu, toolbarMenu, socials } from './data';

//import { useIntl } from 'react-intl';
import { shopInfo, navbarMenu, toolbarMenu, socials } from '~src/_data/';

const Offcanvas = () => {
  return (
    <div id="nav-offcanvas" data-uk-offcanvas="overlay: true; flip: true; mode: slide">
      <aside className="uk-offcanvas-bar uk-padding-remove">
        <div className="uk-card uk-card-default uk-card-small tm-shadow-remove">
          <header className="uk-card-header uk-flex uk-flex-middle">
            <div>
              <a href="" className="uk-link-muted uk-text-bold">
                {shopInfo.phone}
              </a>
              <div className="uk-text-xsmall uk-text-muted" style={{ marginTop: '-2px' }}>
                <div>{shopInfo.address}</div>
                <div>{shopInfo.openingHours}</div>
              </div>
            </div>
          </header>
          <nav className="uk-card-small uk-card-body">
            <ul className="uk-nav-default uk-nav-parent-icon uk-list-divider" data-uk-nav>
              {navbarMenu.map(item => (
                <li key={item.name} className={item.items && 'uk-parent'}>
                  <a className="uk-active" href={item.href}>
                    {item.name}
                  </a>
                  {item.items && (
                    <ul className="uk-nav-parent">
                      {item.items.map(category => (
                        <React.Fragment key={category.name}>
                          <li className="uk-nav-sub uk-text-bold">{category.name}</li>
                          {category.items && (
                            <ul className="uk-nav uk-nav-default uk-nav-sub">
                              {category.items.map((subcategory: { title: string; href: string }) => (
                                <li key={subcategory.title}>
                                  <Link to={subcategory.href}>{subcategory.title}</Link>
                                </li>
                              ))}
                            </ul>
                          )}
                        </React.Fragment>
                      ))}
                    </ul>
                  )}
                </li>
              ))}
            </ul>
          </nav>
          <nav className="uk-card-small uk-card-body">
            <ul className="uk-nav uk-nav-default">
              {toolbarMenu.map(item => (
                <li key={item.name}>
                  <a href={item.href}>{item.name}</a>
                </li>
              ))}
            </ul>
          </nav>
          <nav className="uk-card-body">
            <ul className="uk-iconnav uk-flex-center">
              {socials.map(social => (
                <li key={social.name}>
                  <a href="#" title={social.name} data-uk-icon={social.icon} />
                </li>
              ))}
            </ul>
          </nav>
        </div>
      </aside>
    </div>
  );
};

export default Offcanvas;
