import * as React from 'react';
import { SEO } from './Meta/SEO';
import { useLocation } from '@reach/router';
import { site } from '~src/_data/seo';

export const Metadata = ({ seo }) => {
  const { pathname } = useLocation();
  return (
    <>
      <SEO
        title={seo?.title || site.title}
        titleTemplate={seo?.home === true ? site.desc : site.title}
        titleSeparator={` - `}
        description={seo?.desc || site.desc}
        image={seo?.image || site.image}
        pathname={`${site.url}${pathname}`}
        siteLanguage={site.language}
        siteLocale={site.locale}
        siteDirection={site.direction}
        twitterUsername={site.twitter}
        author={site.title}
        article={seo?.article}
        datePublished={seo?.date}
      />
    </>
  );
};
